<template>
  <div class="home">
    <div class="con">
      <vueRtmpPlayer :playsinline="false" :fluid="true" src="rtmp://ssr.comeboy.cn/live/test"></vueRtmpPlayer>
    </div>
    <div class="buttoncon">
      <i
        class="el-icon-caret-top"
        :class="[lastKey == 0 ? 'light' : '']"
        @mousedown="setIntervalTodoForward"
        @mouseout="clearInterval"
      ></i>
      <br />
      <i
        class="el-icon-caret-left"
        :class="[lastKey == 2 ? 'light' : '']"
        style="margin-right:40px"
        @mousedown="setIntervalTodoLeft"
        @mouseout="clearInterval"
      ></i>
      <i
        class="el-icon-caret-right"
        :class="[lastKey == 3 ? 'light' : '']"
        @mousedown="setIntervalTodoRight"
        @mouseout="clearInterval"
      ></i>
      <br />
      <i
        class="el-icon-caret-bottom"
        :class="[lastKey == 1 ? 'light' : '']"
        @mousedown="setIntervalTodoBack"
        @mouseout="clearInterval"
      ></i>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import "vue-rtmp-player/packages/src/css/custom-theme.css";
import vueRtmpPlayer from "vue-rtmp-player/packages/index.js";
Vue.use(vueRtmpPlayer);
import "video.js/dist/lang/zh-CN";
import { throttle } from "@/tools/fangdou";
import { get } from "@/tools/Axios";
export default {
  name: "Home",
  data() {
    return {
      KEY: {
        up: 0,
        down: 1,
        left: 2,
        right: 3
      },
      downKeys: [],
      forward: null,
      back: null,
      left: null,
      right: null,
      inter: null,
      ws: null
    };
  },
  computed: {
    lastKey: function() {
      const downKeys = this.downKeys;
      return downKeys[downKeys.length - 1];
    }
  },
  mounted() {
    this.forward = throttle(this._forward, 300);
    this.back = throttle(this._back, 300);
    this.left = throttle(this._left, 300);
    this.right = throttle(this._right, 300);
    this.$window.document.addEventListener("keydown", this.handleKeydown);
    this.$once("hook:beforeDestory", function() {
      this.$window.document.removeEventListener("keydown", this.handleKeydown);
    });

    this.$window.document.addEventListener("keyup", this.handleKeyup);
    this.$once("hook:beforeDestory", function() {
      this.$window.document.removeEventListener("keyup", this.handleKeyup);
    });
    this.$window.document.addEventListener("mouseup", this.clearInterval);
  },
  methods: {
    _forward: function() {
      console.log(0);
      get({
        url: "/test.php",
        data: {
          action: "f"
        }
      });
    },
    _back: function() {
      console.log(1);
      get({
        url: "/test.php",
        data: {
          action: "b"
        }
      });
    },
    _left: function() {
      console.log(2);
      get({
        url: "/test.php",
        data: {
          action: "l"
        }
      });
    },
    _right: function() {
      console.log(3);
      get({
        url: "/test.php",
        data: {
          action: "r"
        }
      });
    },
    stop: function() {
      console.log("stop");
      get({
        url: "/test.php",
        data: {
          action: "s"
        }
      });
    },
    pushKey: function(key) {
      console.log("pushKey", key);
      const downKeys = this.downKeys,
        index = downKeys.indexOf(key);
      if (index != -1) {
        downKeys.splice(index, 1);
      }
      downKeys.push(key);
    },
    spliceKey: function(key) {
      console.log("spliceKey", key);
      const downKeys = this.downKeys,
        index = downKeys.indexOf(key);
      downKeys.splice(index, 1);
    },
    getKey: function(e) {
      const keyCode = e.keyCode,
        KEY = this.KEY;
      let key;
      switch (keyCode) {
        case 38:
          key = KEY.up;
          break;
        case 37:
          key = KEY.left;
          break;
        case 39:
          key = KEY.right;
          break;
        case 40:
          key = KEY.down;
          break;
      }
      return key;
    },
    handleKeydown: function(e) {
      let key = this.getKey(e);
      this.pushKey(key);
    },
    handleKeyup: function(e) {
      let key = this.getKey(e);
      this.spliceKey(key);
    },
    /**鼠标事件 */
    setIntervalTodoForward: function() {
      this.inter = this.$window.setInterval(this._forward, 300);
    },
    setIntervalTodoLeft: function() {
      this.inter = this.$window.setInterval(this._left, 300);
    },
    setIntervalTodoRight: function() {
      this.inter = this.$window.setInterval(this._right, 300);
    },
    setIntervalTodoBack: function() {
      this.inter = this.$window.setInterval(this._back, 300);
    },
    clearInterval: function() {
      this.$window.clearInterval(this.inter);
      this.inter = null;
    }
  },
  watch: {
    downKeys: function(downKeys) {
      const lastKey = this.lastKey,
        KEY = this.KEY;
      if (downKeys.length == 0) {
        this.stop();
        return;
      }
      switch (lastKey) {
        case KEY.up:
          this.forward();
          break;
        case KEY.down:
          this.back();
          break;
        case KEY.left:
          this.left();
          break;
        case KEY.right:
          this.right();
          break;
      }
    }
  },
  components: {
    vueRtmpPlayer
  }
};
</script>
<style lang="stylus" scope>
.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .con {
    width: 100%;
    max-width: 900px;
  }

  .buttoncon {
    text-align: center;
    font-size: 32px;
  }
}
</style>
<style>
.light {
  color: #5cb6ff;
}
</style>
